import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { NewCourseCategoryPageComponent } from './new-course-category-page.component';

const routes: Routes = [
  { path: '', component: NewCourseCategoryPageComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class NewCourseCategoryPageRoutingModule { }
